.contact-form {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

form {
    max-width: 600px;
    width: 100%;
    background: #385954;
    padding: 25px 25px 30px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(245, 245, 245, 0.1);
    color: #ffffff;
    margin: 25px;
}

form h2 {
    font-size: 30px;
    text-align: center;
}

form .input-box {
    margin-top: 20px;
}

.input-box .field {
    width: 100%;
    height: 50px;
    background: transparent;
    border: 2px solid #ffffff;
    outline: none;
    border-radius: 6px;
    padding: 15px;
    font-size: 16px;
    color: #ffffff;
    margin-top: 8px;
}

.input-box .field.message {
    height: 200px;
    resize: none;
}

form button {
    width: 100%;
    height: 55px;
    background: #608b94;
    border: none;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    margin-top: 25px;
    transition: 0.5s;
}

form button:hover {
    background: #184788;
}